<style lang="scss">
  #controlList{
    position: absolute;
    left: 0;
    bottom:320px;
    color:#fff;
    font-size:12px;
    background-color: rgba(1, 63, 166, 0.5);
    transition: left 0.3s;
    &.active{
      left: -122px;
      .fa-icon{
        transform: rotate(180deg);
        transform-origin:50% 50%;
      }
    }
    &:after{
      display: block;
      height: 90px;
      width: 10px;
      cursor:pointer;
      background: url('../../../../../assets/image/controllist-slidebar.png') right center no-repeat;
      position: absolute;
      right: -10px;
      content: ' ';
      top: 0;
    }
    .fa-icon{
      position: absolute;
      right: -7px;
      top: 40px;
      z-index: 1;
      cursor: pointer;
    }
    li{
      line-height: 30px;
      height: 30px;
      padding:0 10px;
      // margin:10px;
      &:nth-of-type(2){
        border-top: 1px solid rgb(20, 101, 249);
        border-bottom: 1px solid rgb(20, 101, 249);
      }
    }
  }
</style>
<template>
  <ul id="controlList" :class="{active: isActive}" @click="slideUp()">
    <li>可控服务器：252</li>
    <li>可控设备：58</li>
    <li>可控机构：236</li>
    <icon name="caret-left"></icon>  
  </ul>
</template>
<script type="text/ecmascript-6">
  import {mapGetters} from 'vuex';
  export default {
    data () {
      return {
        isActive: false
      }
    },
    computed: {
      ...mapGetters(['getAbilityQueryKeyword'])
    },
    methods: {
      slideUp (){
        this.isActive = !this.isActive;
      }
    }
  }
</script>





